'use client';
import Link from 'next/link';
import {motion} from 'framer-motion';
import {posts} from '@/lib/blog';
import {Button} from './ui/button';
import {BlogPostCard} from './blog-post-card';

const containerVariants = {
  hidden: {opacity: 0},
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1,
    },
  },
};

export function BlogTeaser() {
  const teaserPosts = posts.slice(0, 3);

  return (
    <motion.section
      className="py-24 sm:py-32"
      initial="hidden"
      whileInView="visible"
      variants={containerVariants}
      viewport={{once: true, amount: 0.2}}
    >
      <div className="container mx-auto max-w-[1200px] px-4">
        <div className="text-center">
          <h2 className="text-3xl font-bold tracking-tight sm:text-4xl">
            From the Blog
          </h2>
          <p className="mx-auto mt-4 max-w-2xl text-lg text-muted-foreground">
            Stay updated with our latest insights on sportswear technology,
            sustainability, and performance.
          </p>
        </div>
        <motion.div
          className="mt-16 grid grid-cols-1 gap-8 md:grid-cols-2 lg:grid-cols-3"
          variants={containerVariants}
        >
          {teaserPosts.map(post => (
            <BlogPostCard key={post.slug} {...post} />
          ))}
        </motion.div>
        <div className="mt-12 text-center">
          <Button asChild variant="outline" size="lg" className="active:scale-95">
            <Link href="/blog">Visit Our Blog</Link>
          </Button>
        </div>
      </div>
    </motion.section>
  );
}
